@import (less) "common.less";

@jungleGreen: #2ABB9B;

@mainTheme: @greensea;
@buttonGreen: @jungleGreen;
@buttonHover: @patronus;
@crimson-red: #D24D57;
@danger: @alizarin;
@important: @pomegranade;
@active: @wetasphalt;
@unimportant: @asbestos;
@neutral: @clouds;
@grey: @normal-grey;
@warning: #E29C45;

.multiselect-wrap{
  margin:0 !important;
  .multiselect{
    background: @white;
    border: 1px solid #e3e3e3;
    border-bottom: none;
    padding: 10px;
    margin: 0;
    ul{
      padding:0;
    }
    li{
      list-style: none;
      display: inline-block;
      padding: 6px;
      color: @white;
      margin: 2px 0;
      background: @patronus;
      border-radius: 7px;
    }
    span.fa{
      font-size: 13px;
      margin-left: 5px;
      position: relative;
      float: right;
      top: -3px;
      cursor: pointer;
    }
  }
  input{
    width: 100%;
    margin: 0;
    display: block;
    top: -4px;
    position: relative;
    border: 1px solid #e3e3e3;
    &:focus{
      outline: none;
    }
    &.full{
      border-top: none;
    }
  }
}
.knockout-autocomplete{
  &.menu {
    background-color: @white;
    padding: 3px 0;
    height: auto;
    width: 44%;
    li {
      text-transform: inherit;
      font-family: 'Roboto Regular';
      font-size: 15px;
      line-height: 18px;
      cursor: pointer;
      list-style: none;
      margin: 0px 0;
      padding: 3px 12px;
      display: block;
      background: @white;
      color: @black;
      &.selected{
        background: @patronus;
        color: @white;
      }
    }
  }
  &.floating-menu {
    position: fixed;
    border: thin #aaa solid;
    margin: 0;
    margin-top: 3px;
    -webkit-box-shadow: 1px 1px 4px rgba(50, 50, 50, 0.75);
    -moz-box-shadow:    1px 1px 4px rgba(50, 50, 50, 0.75);
    box-shadow:         1px 1px 4px rgba(50, 50, 50, 0.75);
  }
  &.after-menu {
    position: absolute;
  }
}

.a-style-none{
  a, a:hover{
    margin:0;
    padding:0;
    color: @black;
    text-decoration: none;
    border-bottom: 0;
  }
}

h3{
  color: @filch;
}

input{
  background: @white;
  padding: 0 5px;
  height: 30px;
  font-size: 15px;
  border:1px solid #d3d3d3;
}
.inline{
  display: inline-block;
}
.block{
  display: block;
}

.items-head, .layer-head{
  margin: 15px 0;
  label.adder{
    float: right;
    line-height: 22px;
  }
  h1{
    display: inline;
  }
  h3{
    color: @filch;
  }
}
.items-body, .layer-body{
  margin: 20px 0;
}
