.items{
  &.disciplines{
    .item .tags{
      text-align: right;
      float:right;
      min-width: 80px;
    }
  }
}
.item{
  background: @patronus-shade;
  color: @filch;
  font-size: 16px;
  padding: 9px;
  cursor: pointer;
  margin: 1px 0;
  &:hover, &.current{
    background: @patronus;
    color: @white;
    span.tag{
      color: @white;
    }
  }
  &.no-hover{
    &:hover{
      background: @patronus-shade;
      color: @filch;
    }
  }
  span.tag{
    color: @patronus-shade;
    margin: 0 8px;
    font-size: 23px;
  }
}

.items{
  .result{
    .a-style-none();
    a{
      display: block;
      width: 100%;
      border:1px solid @akcio;
      &:hover{
        border: 1px solid @patronus;
      }
    }
  }
}

.details-column.tile-boredom{
  background: @patronus-shade;
  width: 150px;
  height: 100px;
  padding: 5px 15px;
  span{
    display: block;
    font-size: 15px;
    color: @akcio;
    line-height: 15px;
    margin: 7px;
    font-weight: bold;
    cursor: pointer;
    &:hover{
      color: @filch;
    }
  }
}

.item.plan{
  input{
    width: 240px;
    height: 38px;
  }
}

.details-column{
  margin: 1%;
  input[type=text], input[type=password], select, textarea{
    width: 100%;
    &.time{
      width: 50px;
      text-align:center;
    }
    &.attempts{
      width: 130px;
      text-align: center;
    }
  }
}

.details-row.float-buttons{
  min-height: 60px;
  button{
    float:right;
    margin: 0 1% 0 0;
  }
}
.details-row.variants{
  input{
    width: 93%;
    height: 40px;
  }
  button{
    float: right;
  }
}
.details-row.theme{
  input{
    width: 87%;
    height: 40px;
  }
  span{
    min-width:100px;
    width:15%;
  }
}
.details-row.date{
  label{
    float: right;
    position: relative;
    top: -20px;
    right: 5px;
    color: @akcio;
    font-size: 12px;
  }
}
.details-row.special-item{
  border-top: 1px solid @akcio;
  border-bottom: 1px solid @akcio;
  margin-top: -1px;
  cursor: pointer;
  font-size: 16px;
  &:last-child{
    margin-bottom: -1px;
  }
  &:hover, &.current{
    background: @patronus;
    color: @white;
    span.tagged{color: @white;}
  }
  span.tagged{
    color: @patronus;
    float: right;
  }
}
.details-row.special-item-details{
  background: @patronus-shade;
  color: @filch;
  span.icon{
    font-size: 25px;
    margin: 0 20px 0 0;
    float: left;
    padding: 0px;
    width: 20px;
  }
  span.text{
    white-space: pre-wrap;
    font-size: 13px;
    display: table;
    text-align: justify;
  }
  .result-setter input{
    max-width: 83px;
  }
}

.stripe-table{
  width: 100%;
  padding: 15px 0;
  border-collapse: collapse;
  margin: 10px 0;
  th{
    padding: 10px;
  }
  tbody tr{
    &:nth-child(odd){
      background: @patronus-shade;
    }
    &:nth-child(even){
      background: @silver;
    }
  }
  td{
    padding: 5px;
  }
  button.remove{
    background: @crimson-red;
  }
}
table.paramset{
  button.remove{
    margin-right: 10px;
  }
  td{
    width: 40%;
    white-space: pre-wrap;
    &:first-child{
      width: 5%;
      text-align: center;
    }
    &:last-child{
      width: 11%;
      text-align: right;
    }
  }
}
table.themes{
  td{
    width: 80%;
    &:first-child{
      width: 5%;
      text-align: center;
    }
    &:last-child{
      width: 11%;
      text-align: right;
    }
  }
  td[colspan]{
    text-align: center;
    font-size: 19px;
    padding: 12px;
    @color: @filch;
    &:hover{
      cursor: pointer;
      background: @patronus-shade;
      color: @filch;
    }
  }
}
table.variants{
  button.remove{
    margin-right: 10px;
  }
  td{
    width: 60%;
    text-align: center;
    &:first-child{
      width: 5%;
      text-align: center;
    }
    &:nth-child(2){
      text-align: left;
      white-space: pre-wrap;
    }
    &:last-child{
      width: 11%;
      text-align: right;
    }
  }
}
table.questions{
  th:last-child{text-align: right}
  td{
    width: 1%;
    white-space: pre-wrap;
    &:first-child{
      width: 40%;
    }
    &:nth-child(2){
      width: 20%;
    }
    &:last-child{
      width: 11%;
      text-align: right;
    }
  }
}

.action-button{
  width: 100%;
  background: @patronus-shade;
  color: @filch;
  cursor: pointer;
  &:hover{
    color: @white;
    background: @patronus;
  }
}
.filter.theme{
  position: absolute;
}


.image-uploader{
  margin:0 !important;
  .row{
    margin-bottom: 15px;
    margin-right: -15px;
    margin-left: -15px;
    &:after{
      clear: both;
    }
    .img-preview{
      width: 25%;
      float:right;
    }
    .file-input{
      width: 70%;
      left: 10px;
      float: left;
      position: relative;
      z-index: 2;
      input{
        width: 70% !important;
        height: 35px;
        padding: 5px;
      }
    }
  }
  .upload-btn{
    padding: 7px;
    top: 0px;
    color: @white;
    padding: 8px;
    background: @patronus;
    &:hover{
      background: @patronus;
    }
  }
  .clean-btn{
    background: @akcio;
    color: @white;
    padding: 8px;
    border-radius: 0px 3px 3px 0;
    &:hover{
      background: @crimson-red;
    }
  }
  img{
    max-width: 70px;
    max-height: 70px;
    width: auto;
    height: auto;
  }
}
.image-holder{
  width: 100px;
  height: 100px;
  text-align: center;
  .remove{
    background: @black;
    opacity: 0.5;
    width: 15px;
    height: 15px;
    text-align: center;
    position: relative;
    z-index: 2;
    color: @white;
    float: right;
    font-size: 15px;
    cursor: pointer;
  }
  img{
    max-width: 100px;
    max-height: 100px;
    width: auto;
    height: auto;
    position: relative;
    cursor: pointer;
  }
}



#compile-modal{
  max-height: 450px;
  overflow: auto;
  h3{
    white-space: pre-wrap;
  }
}

.label(){
  font-size: 12px;
  color: @unimportant;
  span{
    color: @important;
  }
}
.width100{
