.multiselect-width(@wpercent){
  .multiselect-wrap {
    width: @wpercent;
    .multiselect {
      width: @wpercent;
    }
    input{
      width: @wpercent + 0.9;
    }
  }
  .knockout-autocomplete.menu{
    width: @wpercent - 4;
  }
}
