#errors-modal{
  width: 300px;
  h3{
    display: inline-block;
  }
  h4{
    padding: 0 5px 5px;
    margin: 0 5px 5px;
  }
  .fa{
    padding-right:5px;
    font-size: 20px;
    color: @warning;
  }
  .button-holder{
    height: 30px;
    button{
      width: 70px;
      height: 30px;
      font-size: 15px;
      float: right;
    }
  }
}
#code-editor-modal{
  width: 800px;
  height: 450px;
  &#editor{
    position:relative;
  }
  .cancel, .save, #button{
    margin: 15px 0 0;
    color: @white;
    background: @mainTheme;
    cursor: pointer;
    border: 0;
    padding: 10px;
  }
  .cancel{
    background: @crimson-red;
    float: right;
  }
  .save{
    background: @buttonGreen;
    margin-right: 5px;
    float: right;
  }
  .ace_editor{
    width: 100%;
    height: 87%;
    border: 1px solid #e3e3e3;
  }
}

