.menu{
  top: 0;
  background: @white;
  height: 50px;
  width: 100%;
  margin: 0 auto;
  position: fixed;
  border-bottom: 1px solid @active;
  z-index: 10;
  ul{
    text-align: center;
  }
  li {
    color: @midnightblue;
    list-style: none;
    display: inline;
    padding: 20px 20px;
    font-size: 15px;
    text-transform: uppercase;
    vertical-align: sub;
    &:hover{
      color: @white;
      background: @midnightblue;
      cursor: pointer;
    }
    a{
      border:0;
      color:@black;
      &:hover{
        color:@white;
      };
    }
  }
}

