.org-accordion{
  width: 100%;
  height: 100%;
  .org-item{
    width: 100%;
    line-height: 50px;
    height: 50px;
    background: @mainTheme;
    color: @neutral;
    text-align: center;
    text-transform: uppercase;
    border-bottom: 2px solid @neutral;
    cursor: pointer;
    &:hover{
      background: @active;
    }
    &:active{
      background: @active;
    }
    &-current{
      background: @active;
      &:hover{
        background: @active;
      }
    }
    &.text-alternative{
      text-transform: none;
      font-size: 17px;
      font-weight: bold;
    }
  }
}

.org-info{
  .width100();
  div{
    display: inline-block;
    margin: 10px 15px;
  }
  label{
    .label();
  }
  input{
    padding: 6px;
    width: 150px;
  }
}

