  float: right;
  border-bottom: 0;
  &:hover{
    color: @black;
  }
}
button.move {
  width: 150px;
  &-section {
    width: 160px;
  }
}
button.add-section{
  width: 300px;
  background: @peterriver;
  &:hover{
    background: @belizehole;
  }
}
