  background: @buttonGreen;
  width: 50px;
  &:hover{
    background: @buttonHover;
  }
}

.discipline{
  &-edit{
    div{
      &:first-child{
        width: 20%;
        input{
          width: 100%;
        }
      }
      &:nth-child(2){
        width: 70%;
        input{
          width: 94%;
        }
      }
      &:nth-child(3){
        width: 93%;
        .multiselect-width(97%);
        //select{
        //  width: 97%;
        //}
      }
    }
    .float-btn-group{
      float: right;
      margin-right: 8%;
    }
  }
  &-info{
    div{
      &:last-child{
        width: 100%;
        i{
          &:first-child{
            float: left;
          }
          &:last-child{
            float:right;
            margin-right: 8%;
          }
        }
      }
    }
  }
}

.themes{
  .org-info{
    border-bottom: 20px solid @white;
    .main-info{
      padding: 5px;
      div{
        width: 94%;
      }
      input{
        min-width: 300px;
        width: 88%;
      }
    }
  }
  .filter div{
    width: 32%;
  }
  &-add{
    border-bottom: 20px solid @white;
    .sq-small{
      width: 35px;
      height: 34px;
      margin-left: 10px;
    }
    .select-theme, .select-complexity{
      width: 46%;
      select{
        width: 99%;
        margin: 0;
      }
    }
    .time{
      width: 13%;
      position: relative;
      top: 20px;
      input{
        width: 45px;
        font-size: 25px;
        text-align: center;
      }
    }
    .question-text{
      width: 97%;
      textarea{
        width: 98%;
        height: 100px;
      }
    }
    .answers{
      &-input{
        width: 96%;
        input{
          width: 94%;
        }
      }
      &-table{
        width:96%;
        table{
          width: 100%;
          border-top: 1px solid @unimportant;
          border-bottom: 1px solid @unimportant;
          padding:7px;
          td{
            width:65%;
            &:first-child{
              width: 5%;
            }
            &:last-child{
              width: 20%;
            }
          }
        }
      }
    }
    .btn-larger-group{
      width:100%;
        button{
          float: right;
          width:200px;
          margin-right: 45px;
        }
    }
    .upload-image{
      width: 80%;
      position: relative;
      top: -17px;
      margin: 0;
      div{
        display: block;
        margin: 0;
      }

    }
    .code{
      width: 97%;
      textarea{
        height: 50px;
        width: 49%;
      }
      .save-params{
        float:right;
        margin-right: 15px;
      }
      table{
        width: 98%;
        td{
          white-space: pre-wrap;
          &.input, &.output{
            width: 50%;
          }
          &:last-child{
            text-align: center;
          }
        }
      }
    }
  }
}
.test{
  .org-info{
    &-details{
      width: 95%;
      .name{
        width: 45%;
      }
      .type{
        width: 15%;
      }
      .time{
        width: 7%;
      }
      .btn-group{
        float: right;
      }
    }
    &-edit{
      width: 94%;
      .name{
        width: 68%;
        input{
          width: 97%;
        }
      }
      .type{
        width: 25%;
        select{
          width: 100%;
          height: 32px;
          margin: 0;
          font-size: 15px;
        }
      }
      .time, .tryouts{
        width: 15%;
        input{
          width: 35px;
          font-size: 15px;
          text-align: center;
        }
      }
      .tryouts{
        width: 50%;
      }
      .random{
        width: 20%;
      }
      .themes{
        width: 97%;
        .multiselect-width(99%);
      }
      .btn-group{
        float: right;
        &:after{
          clear: both;
        }
      }
    }
  }
}


.institutes{
  width: 100%;
  height: 100%;
  .institute{
    width: 100%;
    line-height: 50px;
    height: 50px;
    background: @mainTheme;
    color: @neutral;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    border-bottom: 1px solid @neutral;
    &.font-settings{
      text-transform: none;
      font-size: 17px;
      font-weight: bold;
    }
    &:hover{
      background: @active;
    }
    &:active{
      background: @active;
    }
    &-current{
      background: @active;
      &:hover{
        background: @active;
      }
    }
  }
  .profiles{
    width: 100%;
    background: @light-grey;

    .profile{
      background: @mainTheme;
      color: @neutral;
      width: 250px;
      height: 150px;
      margin: 5px 10px 5px 0px;
      line-height: 45px;
      float: left;
      text-align: center;
      cursor: pointer;
      font-weight: bold;
      &:hover{
        background: @buttonHover;
      }
      &:active{
        background: @active;
      }
      button:hover{
        background: @active;
      }
    }
  }
}

.info-group{
  .details-group{
    div{
      display: inline-block;
      margin: 10px 15px;
    }
  }
  div{
    display: inline-block;
    margin: 10px 15px;
  }
  label{
    font-size: 12px;
    color: @unimportant;
  }
  .study-form{
    &-selected{
      font-size: 18px;
      color: @mainTheme;
    }
    font-size: 13px;
    color: @unimportant;
    line-height: 1.8;
    cursor: pointer;
  }
  .group-name{
    input{
      width: 200px;
    }

  }
  input{
    padding: 6px;
    width: 70px;
  }
}

.tyle-btn{
  color: @neutral;
  width: 170px;
  background: @active;
  &-negative{
    border: 2px solid @active;
    background: @mainTheme;
  }
}

button.width200{
  width: 200px;
}

button.sq-small{
  width: 35px;
  height: 34px;
  margin-left: 5px;
}



.tooltip_templates {
  display: none;
}


.width60{
  width: 60%;
}

.manual{
  h1{
    text-align: center;
    padding: 30px;
    color: @mainTheme;
  }
  h3{
    padding: 10px 0 0 15px;
  }
  p{
    text-align: justify;
    padding: 10px 30px 20px 0;
  }
  .manual-image{
    max-height: 700px;
    max-width: 700px;
    width: auto;
    height: auto;
  }
}

.expanded-image{
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 50;
  top: 0;
  left: 0;
  position: fixed;
  display: none;
  img{
    display: block;
    margin: 0 auto;
  }
}

.LoadingImage{
  width: 100%;
  height: 100%;
  z-index: 1000;
  opacity: 0.3;
  position: absolute;
  display: none;
  img{
    display: block;
    margin: 0 auto;
    width: 100%;
    margin-top: -20%;
  }
}

.section-info{
  padding-top: 20px;
  padding-bottom: 10px;
  p{
    text-align: center;
  }
}

.section-name{
  padding: 20px;
  input{
    width: 85%;
    margin-left: 10px;
  }
  label{
    font-size: 18px;
  }
}

.section-buttons{
  padding: 10px;
  text-align: center;
  button{
    width: 100px;
  }
}

#sections-modal{
  width: 800px;
  table {
    td:first-child {
      width: 5%;
    }
    td:nth-child(2){
      width: 70%;
    }
    td:last-child {
      width: 15%;
      button.info {
        background: @peterriver;
      }
    }
  }
}


#cancel-modal{
  div{
    text-align: center;
    font-size: 18px;
    padding: 15px;
  }
}

.popup{
  &-delete{
    height: 80px;
    div:last-child{
      float: right;
      button:first-child{
        margin: 0 5px;
      }
    }
  }
  &-theme{
    div{
      margin: 10px 0;
    }
    input{
      width: 95%;
    }
    label{
      .label();
    }
    .popup-btn-group{
      float: right;
      margin: 5px 4px;
    }
  }
}
#add-theme-modal{
  width: 350px;
  height: 160px;
}
.plan-details {
  div {
    display: inline-table;
    margin: 10px 15px 25px;
    width: 11%;
    label{
      font-size: 12px;
      color: @unimportant;
      line-height: 40px;
    }
    &:first-child{
      width: 15%;
    }
    .plan-form{
      &-selected{
        font-size: 18px;
        color: @mainTheme;
      }
      font-size: 13px;
      color: @unimportant;
      line-height: 1.8;
      cursor: pointer;
    }
  }
  &:nth-child(odd) {
    background: @light-grey;
  }
  input{
    width: 70px;
  }
  select{
    width: 200px;
  }

}


